.App {
  text-align: center;
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: crimson;
}

.login-page{
  text-align: center;

}
.login-heading{
  font-weight: 700;
  padding: 20px 0px;
  color: White;
}
.login-box{
  display: flex;
  flex-direction: column;
  margin: 10px;
  padding: 0 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: 400px;
  gap: 40px;
  justify-content: center;
  align-items: stretch;
  background-image: url("https://www.gifimages.pics/images/quotes/english/general/pizza-gif-52650-120750.gif");
  background-repeat: repeat;
  border: 1px solid black;
  border-radius: 50px;
}

.login-reg-admin{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}

.reg-forgotpassword{
  text-align: left;
}
